/**
 * @module M/control/VentanaNegocioControl
 */

import VentanaNegocioImplControl from 'impl/ventananegociocontrol';
import templatesVentanaNegocio from 'templates/ventanaNegocio';


export default class VentanaNegocioControl extends M.Control {

  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor() {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(VentanaNegocioImplControl)) {
      M.exception('La implementación usada no puede crear controles VentanaNegocioControl');
    }
    // 2. implementation of this control
    let impl = new VentanaNegocioImplControl();
    super(impl, "ventanaNegocio");
    this.facadeMap_ = null;

    if (!M.template.compileSync){
      M.template.compileSync = (string, options) => {
        let templateCompiled;
        let templateVars = {};
        let parseToHtml;
        if (!M.utils.isUndefined(options)){
          templateVars = M.utils.extends(templateVars, options.vars);
          parseToHtml = options.parseToHtml;
        }
        const templateFn = Handlebars.compile(string);
        const htmlText = templateFn(templateVars);
        if (parseToHtml !== false) {
          templateCompiled = M.utils.stringToHtml(htmlText);
        } else {
          templateCompiled = htmlText;
        }
        return templateCompiled;
      };
    }
  }
  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
   createView(map) {
    this.facadeMap_ = map;
     return new Promise((success, fail) => {
      const html = M.template.compileSync(templatesVentanaNegocio);
        /** Añadir código dependiente del DOM */
        success(html);
      });
   }
  /**
   * This function is called on the control activation
   *
   * @public
   * @function
   * @api stable
   */
  activate() {
    super.activate(); //calls super to manage de/activation
  }
  /**
   * This function is called on the control deactivation
   *
   * @public
   * @function
   * @api stable
   */
  deactivate() {
    super.deactivate(); //calls super to manage de/activation
  }
  
  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof VentanaNegocioControl;
  }
}
