/**
 * @module M/impl/Control/VentanaNegocioControl
 */

export default class VentanaNegocioControl extends M.impl.Control {
  /**
   * @classdesc
   * Main constructor of the ventanaNegocioControl.
   *
   * @constructor
   * @extends {M.impl.Control}
   * @api stable
   */
  constructor() {
    super();

    this.layersWFS_ = [];
    this.senderos_st_ = null;
    this.equipComposite_st_ = null;
    this.zoomSenderos_ = null;
    this.zoomClusterEquipamientos_ = null;
    this.senderoDesactivada_ = false;
    this.carrilDesactivada_ = false;
    this.featuresFiltradas_ = [];
    this.responseJsonNegocio_ = null;

    this.observerSenderos_ = null;

    this.equipFilter_ = null;
    this.senderoFilter_ = null;
    this.carrilFilter_ = null;
    this.CVFilter_ = null;
    this.PIFilter_ = null;
    this.EcoFilter_ = null;
  }
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    // super addTo - don't delete
    super.addTo(map, html);
  }

  cargaInicial(map, urlJsonNegocio) {
    this.facadeMap_ = map;
    if(M.utils.isUndefined(urlJsonNegocio) || M.utils.isNullOrEmpty(urlJsonNegocio)) {
        M.dialog.error('No se ha indicado el parámetro con la url del fichero de configuración.');
    }else{
      return new Promise((resolve, reject) => {
        M.remote.get(urlJsonNegocio).then((response) => {
          if(response.code == 200) {
            try{
              let responseJson = JSON.parse(response.text);
              this.responseJsonNegocio_ = JSON.parse(response.text);

              //Establecer MaxExtent
              this.setMapMaxExtent(responseJson.maxExtent);
              //Establecer BBOX
              this.setMapBBOX(responseJson.bbox);
              //Establecer WMC
              this.addWMC(responseJson.wmc.url, responseJson.wmc.nombre);

              //CARGA DE CAPAS
              responseJson.capasWFS.forEach((layer) => {
                switch (layer.type.toUpperCase()) {
                  case "WFS":
                  this.createWFSLayer(layer);
                  break;
                  case "GEOJSON":
                  this.createGeoJSONLayer(layer);
                  break;
                }
              });
              //Añadir capas al mapa
              this.addLayers(this.layersWFS_);
              //Establecer filtros
              this.setFiltroEquipamientos();
              this.setFiltroEENNPP();
              this.setFiltroSenderos();
              resolve();
            }catch (e) {
              reject(e);
            }
          }else{
            reject(response.code + ": No se ha encontrado el fichero de configuración del plugin de negocio.");
          }
        });
      });
    }
  }

  ejecutarOperaciones() {
    this.addEvents(this.responseJsonNegocio_.zoomSenderos, this.responseJsonNegocio_.zoomClusterEquipamientos);
    this.gestionarBotonesInfo();
    setTimeout(this.modificarLeyenda.bind(this), 3000);
    setTimeout(this.loadViasPortalesCDAU.bind(this), 3000);
    setTimeout(this.cambiarLeyendaOSM.bind(this), 3000);
    setTimeout(this.setCapaBase.bind(this), 3000);
  }

  setMapBBOX(coordinates) {
    this.facadeMap_.setBbox(coordinates);
  }

  setMapMaxExtent(coordinates) {
    this.facadeMap_.setMaxExtent(coordinates);
  }

  addWMC(url, name) {
    let myWmc = new M.layer.WMC({
      url: url,
      name: name
    });
    this.facadeMap_.addWMC(myWmc);
  }

  createWFSLayer(layer) {
    let layer_st = null;

    //Creamos el estilo
    if(!layer.style.category) {
      layer_st = this.createStyle(layer.geometry, layer.style.style);
    } else {
      layer_st = this.createCategoryStyle(layer.geometry, layer.style.categories);
    }

    //Creamos la capa
    let layerWFS = new M.layer.WFS({
      name: layer.name,
      namespace: layer.namespace,
      legend: layer.legend,
      geometry: layer.geometry,
      url: layer.url,
      version: layer.version
    }, {
      getFeatureOutputFormat: layer.getFeatureOutputFormat,
      describeFeatureTypeOutputFormat: layer.describeFeatureTypeOutputFormat
    });

    layerWFS.on(M.evt.SELECT_FEATURES, (f)=>{
      f.setStyle(null);
    });

    //Controlamos si tiene cluster
    if(M.utils.isUndefined(layer.cluster)){
      if(layer.name == "senderos"){
          this.senderos_st_ = layer_st;
        }else{
          layerWFS.setStyle(layer_st);
        }
    }else{
      let cluster_st = this.createCluster(layer);
      if(!layer_st instanceof M.style.Category){
        layerWFS.setStyle(cluster_st);
      }else{
        let composite = cluster_st.add(layer_st);
        layerWFS.setStyle(composite);
        if(layer.name == "equipamientos_uso_publico") this.equipComposite_st_ = composite;
      }
    }
    
    //Establecemos la visibilidad y si se muestra en el TOC
    layerWFS.setVisible(layer.visibility);
    layerWFS.displayInLayerSwitcher = layer.displayInLayerSwitcher;

    //Añadimos la capa al array de capas
    this.layersWFS_.push(layerWFS);
  }

  createGeoJSONLayer(layer) {
    let layer_st = null;
    if(!layer.style.category) {
      layer_st = this.createStyle(layer.geometry, layer.style.style);
    } else {
      layer_st = this.createCategoryStyle(layer.geometry, layer.style.categories);
    }

    let layerGeoJSON = new M.layer.GeoJSON({ 
      name: layer.name,
      extract: layer.extract,
      source: layer.source
    });

    layerGeoJSON.on(M.evt.SELECT_FEATURES, (f)=>{
      //layerGeoJSON.setStyle(layerGeoJSON.getStyle());
      f.setStyle(null);
      
    });

    layerGeoJSON.setStyle(layer_st);
    layerGeoJSON.setVisible(layer.visibility);
    layerGeoJSON.displayInLayerSwitcher = layer.displayInLayerSwitcher;
    this.layersWFS_.push(layerGeoJSON);

    
  }

  addLayers(layersArray) {
    layersArray.forEach((layer) => {
      this.facadeMap_.addLayers(layer);
    });
  }

  createStyle(geometry, style) {
    let layer_st = null;

    switch (geometry.toUpperCase()) {
      case "POLYGON":
        layer_st = new M.style.Polygon(style);
        break;
      case "LINE":
        layer_st = new M.style.Line(style); 
        break;
      case "POINT":
        layer_st = new M.style.Point(style);
        break;
    }
    return layer_st;
  }

  createCategoryStyle(geometry, categories) {
    let layer_st = null;
    let categoryValues = new Object();

    categories.values.forEach((value) => {
      let value_st = this.createStyle(geometry, value.style);
      categoryValues[value.name] = value_st;        
    });

    layer_st = new M.style.Category(categories.attributeName, categoryValues);

    return layer_st;
  }

  createCluster(layer) {
    let cluster_st = null;
    let clusterOptions = {     
      ranges: [],
      animated: layer.cluster.options.animated,
      hoverInteraction: layer.cluster.options.hoverInteraction,
      displayAmount: layer.cluster.options.displayAmount,
      distance: layer.cluster.options.distance,
      maxFeaturesToSelect: layer.cluster.options.maxFeaturesToSelect
    };

    layer.cluster.options.ranges.forEach((range) => {
      let value = {
        min: range.min,
        max: range.max,
        style: this.createStyle(layer.geometry, range.style)
      };

      clusterOptions.ranges.push(value);
    });

    cluster_st = new M.style.Cluster(clusterOptions, layer.cluster.vendorParameters);

    return cluster_st;
  }

  setFiltroEquipamientos() {
    let equipamientos = this.layersWFS_.find(layer => layer.name == "equipamientos_uso_publico");

    equipamientos.on(M.evt.LOAD, (features) => {
      equipamientos.setFilter(M.filter.EQUAL("TIPOEQUIPA", "CENTRO DE VISITANTES"));
      let centros_visitantes = this.layersWFS_.find(layer => layer.name == "Centros de Visitantes");    
      centros_visitantes.addFeatures(equipamientos.getFeatures());

      equipamientos.setFilter(M.filter.EQUAL("TIPOEQUIPA", "PUNTO DE INFORMACION"));
      let puntos_informacion = this.layersWFS_.find(layer => layer.name == "Puntos de Información");     
      puntos_informacion.addFeatures(equipamientos.getFeatures());

      equipamientos.setFilter(M.filter.EQUAL("TIPOEQUIPA", "ECOMUSEO"));
      let ecomuseos = this.layersWFS_.find(layer => layer.name == "Ecomuseos");     
      ecomuseos.addFeatures(equipamientos.getFeatures());

      let initialFilter = M.filter.NOT(M.filter.OR(
        [M.filter.EQUAL("TIPOEQUIPA", "CENTRO DE VISITANTES"),
        M.filter.EQUAL("TIPOEQUIPA", "PUNTO DE INFORMACION"), 
        M.filter.EQUAL("TIPOEQUIPA", "ECOMUSEO")]));
      equipamientos.setFilter(initialFilter);
    });
  }

  setFiltroEENNPP() {
    let eennpp = this.layersWFS_.find(layer => layer.name == "EENNPP");

    eennpp.on(M.evt.LOAD, (features) => {     
      eennpp.setFilter( M.filter.EQUAL("FIGURA", "Parque Periurbano" ));
      let parquePeriurbano = this.layersWFS_.find(layer => layer.name == "Parques Periurbanos");     
      parquePeriurbano.addFeatures(eennpp.getFeatures());

      eennpp.setFilter( M.filter.EQUAL("FIGURA", "Reserva Natural Concertada" ));
      let reservaNaturalConcertada = this.layersWFS_.find(layer => layer.name == "Reservas Naturales Concertadas");    
      reservaNaturalConcertada.addFeatures(eennpp.getFeatures());

      eennpp.setFilter(M.filter.OR(
        [M.filter.EQUAL("FIGURA", "Monumento Natural"),
        M.filter.EQUAL("FIGURA", "Zona de protección del Monumento Natural")]));
      let monumentoNatural = this.layersWFS_.find(layer => layer.name == "Monumentos Naturales");         
      monumentoNatural.addFeatures(eennpp.getFeatures());

      eennpp.setFilter( M.filter.EQUAL("FIGURA", "Paisaje Protegido" ));
      let paisajeProtegido = this.layersWFS_.find(layer => layer.name == "Paisajes Protegidos");      
      paisajeProtegido.addFeatures(eennpp.getFeatures());

      eennpp.setFilter(M.filter.OR(
        [M.filter.EQUAL("FIGURA", "Reserva Natural"),
        M.filter.EQUAL("FIGURA", "Zona de protección de la Reserva Natural")]));
      let reservaNatural = this.layersWFS_.find(layer => layer.name == "Reservas Naturales");             
      reservaNatural.addFeatures(eennpp.getFeatures());  

      eennpp.setFilter( M.filter.EQUAL("FIGURA", "Paraje Natural" ));
      let parajeNatural = this.layersWFS_.find(layer => layer.name == "Parajes Naturales");      
      parajeNatural.addFeatures(eennpp.getFeatures());

      eennpp.setFilter( M.filter.EQUAL("FIGURA", "Parque Natural" ));
      let parqueNatural = this.layersWFS_.find(layer => layer.name == "Parques Naturales");
      parqueNatural.addFeatures(eennpp.getFeatures());    

      eennpp.setFilter(M.filter.OR(
        [M.filter.EQUAL("FIGURA", "Parque Nacional"),
        M.filter.EQUAL("FIGURA", "Zona de protección del Parque Nacional")]));
      let parqueNacional = this.layersWFS_.find(layer => layer.name == "Parques Nacionales"); 
      parqueNacional.addFeatures(eennpp.getFeatures());   
      
      eennpp.setVisible(false);
    });
  }

  setFiltroSenderos() {
    let senderosycarriles = this.layersWFS_.find(layer => layer.name == "senderos");

    senderosycarriles.on(M.evt.LOAD, (features) => {
      senderosycarriles.setFilter(M.filter.EQUAL("MODALIDAD", "Carril cicloturista")); 
      let carriles = this.layersWFS_.find(layer => layer.name == "Carriles Cicloturista");    
      carriles.addFeatures(senderosycarriles.getFeatures());

      senderosycarriles.setFilter(M.filter.EQUAL("MODALIDAD", "Sendero señalizado"));    
      senderosycarriles.setStyle(this.senderos_st_);

      senderosycarriles.setVisible(false);
      carriles.setVisible(false);
    });
  }

  gestionarBotonesInfo() {
    //Debe estar añadido el plugin de infowfs al panel de navegación
    let navPluginPanel = this.facadeMap_.getPanels("navigation")[0];
    if(!M.utils.isUndefined(this.facadeMap_.getPanels("navigation")[0])){ 
      let controles = navPluginPanel.getControls();
      let ctrlInfoWFS = controles.find((element) => {
        return element.name == "infoWFS";
      });

      let ctrlInfoWMS = controles.find((element) => {
        return element.name == "getfeatureinfo";
      });

      //Necesario para cargar el info WMS en el panel de navegación y borrar el otro panel
      let toolsPanel = this.facadeMap_.getPanels("tools")[0];
      let toolsControls = toolsPanel.getControls();
      let ctrlInfoWMSTools = toolsControls.find((element) => {
        return element.name == "getfeatureinfo";
      });

      if(!M.utils.isUndefined(ctrlInfoWMSTools)) {
        ctrlInfoWMSTools.on(M.evt.ADDED_TO_PANEL, () => {
          ctrlInfoWMSTools.getElement().remove();
        });
      } 

      //Cambiamos el icono y el tooltip del info WMS
      if(!M.utils.isUndefined(ctrlInfoWMS)) {
        ctrlInfoWMS.on(M.evt.ADDED_TO_PANEL, () => {
          document.getElementById("m-getfeatureinfo-button").title = "Consultar capas temáticas";
          document.getElementById("m-getfeatureinfo-button").classList.add("g-cartografia-info-tematica");
          document.getElementById("m-getfeatureinfo-button").classList.remove("g-cartografia-info");
        });
      }

      //Activar info WFS
      ctrlInfoWFS.on(M.evt.ADDED_TO_PANEL, () => {
        if(!M.utils.isUndefined(ctrlInfoWFS)) ctrlInfoWFS.activate();
      });      
    }    
  }

  modificarLeyenda() {
    if(!M.utils.isUndefined(this.facadeMap_.getPanels("managelayers")[0])){
      this.facadeMap_.getControls("ActiveLayers")[0].on(M.evt.ACTIVATED, () => {
        let target = document.getElementById("tool-activelayers");
        // let target = this.facadeMap_.getControls("ActiveLayers")[0].getControlContainer_();

        let observer = new MutationObserver((mutations) => {
          mutations.forEach((mutation) => {
            //Equipamientos
            if(document.getElementById("m-waiting-result").classList.contains("dNone") && target.querySelector("div[id*='equipamientos_uso_publico'] img[data-id*='equipamientos_uso_publico']").getAttribute("src") != "data:image/png;base64,"){
              target.querySelector("div[id*='equipamientos_uso_publico'] img[data-id*='equipamientos_uso_publico']").setAttribute("src", "data:image/png;base64,");
            }
            //Red Natura
            if(document.getElementById("m-waiting-result").classList.contains("dNone") && target.querySelector("div[id*='RedNatura2000'] img[data-id*='RedNatura2000']").getAttribute("src") != "data:image/png;base64,"){
              target.querySelector("div[id*='RedNatura2000'] img[data-id*='RedNatura2000']").setAttribute("src", "data:image/png;base64,");
            }
            //Senderos
            if(document.getElementById("m-waiting-result").classList.contains("dNone") && target.querySelector("div[id*='senderos'] img[data-id*='senderos']").getAttribute("src") != "data:image/png;base64,"){
              target.querySelector("div[id*='senderos'] img[data-id*='senderos']").setAttribute("src", "data:image/png;base64,");
            }
            //Selección
            if(document.querySelector("div[id*='Entidades Seleccionadas']") != null && document.getElementById("m-waiting-result").classList.contains("dNone") && target.querySelector("div[id*='Entidades Seleccionadas'] img[data-id*='Entidades Seleccionadas']").getAttribute("src") != "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABcAAAANCAIAAADNBWIKAAAAMUlEQVR42mP8//8/A8WAEWhKRt0OSoyY0eRBbVOAHFL1wzWOmjKUTCEbUNUUSoyAAAAEblzn4Fg0eQAAAABJRU5ErkJggg=="){
              target.querySelector("div[id*='Entidades Seleccionadas'] img[data-id*='Entidades Seleccionadas']").setAttribute("src", "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABcAAAANCAIAAADNBWIKAAAAMUlEQVR42mP8//8/A8WAEWhKRt0OSoyY0eRBbVOAHFL1wzWOmjKUTCEbUNUUSoyAAAAEblzn4Fg0eQAAAABJRU5ErkJggg==");
            }
          });
        }); 

    let config = { attributes: true, childList: true, characterData: true, subtree: true };

    observer.observe(target, config);

      });
    }
  }

  loadViasPortalesCDAU() {
    if(!M.utils.isUndefined(this.facadeMap_.getPanels("managelayers")[0])){
      this.facadeMap_.getControls("BaseLayers")[0].on(M.evt.ACTIVATED, () => {
        let controlContainer = document.getElementById("tool-baselayers");
        // let controlContainer = this.facadeMap_.getControls("BaseLayers")[0].getControlContainer_();
        let target = controlContainer.querySelector("div[id='toc-baselayers']");

        let observer = new MutationObserver((mutations) => {
          mutations.forEach((mutation) => {
            let baseCDAU = this.facadeMap_.getWMS("CDAU_base")[0];
            if(baseCDAU.isbaseLayer && baseCDAU.isVisible()){
              //Cargamos los portales la primera vez que se selecciona la base de CDAU
              if(this.facadeMap_.getWMS("CDAU_wms").length == 0){
                let cdauWMS = new M.layer.WMS({
                  url: 'http://www.callejerodeandalucia.es/servicios/cdau/wms?',
                  name: 'CDAU_wms',
                  legend: 'CDAU Vías y Portales',
                  transparent: true,
                  tiled: true
                });
                this.facadeMap_.addWMS(cdauWMS);
                cdauWMS.setVisible(false);
                cdauWMS.displayInLayerSwitcher = false;
              }
              //Activamos vías y portales       
              this.facadeMap_.getWMS("CDAU_wms")[0].setVisible(true);
            }else{
              //Desactivamos vías y portales
              if(!M.utils.isUndefined(this.facadeMap_.getWMS("CDAU_wms")[0])) this.facadeMap_.getWMS("CDAU_wms")[0].setVisible(false);
            }
          });
        }); 

    let config = { attributes: true, childList: true, characterData: true, subtree: true };

    observer.observe(target, config);

      });
    }
  }

  addEvents(zoomSenderos, zoomClusterEquipamientos) {
    //Visibilidad de senderos
    this.zoomSenderos_ = zoomSenderos;
    let senderos = this.layersWFS_.find(layer => layer.name == "senderos");
    senderos.on(M.evt.LOAD, (features) => {
      this.gestionSenderos(zoomSenderos);
      this.facadeMap_.getMapImpl().getView().on('change:resolution', () => {this.visibilidadSenderos(this.zoomSenderos_);});
    });

    //Visibilidad del cluster de equipamientos
    this.zoomClusterEquipamientos_ = zoomClusterEquipamientos;
    let equipamientos = this.layersWFS_.find(layer => layer.name == "equipamientos_uso_publico");
    equipamientos.on(M.evt.LOAD, (features) => {
      this.facadeMap_.getMapImpl().on('moveend', () => {this.gestionClusterEquipamientos(this.zoomClusterEquipamientos_);});
    });
  }

  visibilidadSenderos(zoomSenderos) {
    let senderos = this.layersWFS_.find(layer => layer.name == "senderos");
    let carriles = this.layersWFS_.find(layer => layer.name == "Carriles Cicloturista");
    
    if(this.facadeMap_.getZoom() >= zoomSenderos && !this.senderoDesactivada_){
      senderos.setVisible(true);
    } else {
      senderos.setVisible(false);
    }

    if(this.facadeMap_.getZoom() >= zoomSenderos && !this.carrilDesactivada_){
      carriles.setVisible(true);
      carriles.setStyle(new M.style.Line({fill: {}, stroke: {color: 'rgb(175,48,83)', width: 4, linedash: [15,7]}}));
    } else {
      carriles.setVisible(false);
    }
  }

  gestionSenderos(zoomSenderos) {
    let senderos = this.layersWFS_.find(layer => layer.name == "senderos");
    let carriles = this.layersWFS_.find(layer => layer.name == "Carriles Cicloturista"); 

    if(!M.utils.isUndefined(this.facadeMap_.getPanels("managelayers")[0])){
      // this.facadeMap_.getControls("ActiveLayers")[0].on(M.evt.ACTIVATED, () => {
        let target = document.getElementById("tool-activelayers");
        // let target = this.facadeMap_.getControls("ActiveLayers")[0].getControlContainer_();

        this.observerSenderos_ = new MutationObserver((mutations) => {
          mutations.forEach((mutation) => {
            if(this.facadeMap_.getZoom() >= zoomSenderos && !senderos.isVisible()){
              this.senderoDesactivada_ = true;
            }else if(this.facadeMap_.getZoom() >= zoomSenderos && senderos.isVisible()){
              this.senderoDesactivada_ = false;
            }

            if(this.facadeMap_.getZoom() >= zoomSenderos && !this.senderoDesactivada_){
              senderos.setVisible(true);
            } else {
              senderos.setVisible(false);
            }

            if(this.facadeMap_.getZoom() >= zoomSenderos && !carriles.isVisible()){
              this.carrilDesactivada_ = true;
            }else if(this.facadeMap_.getZoom() >= zoomSenderos && carriles.isVisible()){
              this.carrilDesactivada_ = false;
              //carriles.setStyle(new M.style.Line({fill: {color: "yellow"}} ));
            }

            if(this.facadeMap_.getZoom() >= zoomSenderos && !this.carrilDesactivada_){
              carriles.setVisible(true);
              carriles.setStyle(new M.style.Line({fill: {}, stroke: {color: 'rgb(175,48,83)', width: 4, linedash: [15,7]}}));

            } else {
              carriles.setVisible(false);
            }            
          });    
        });

        let config = { attributes: true, childList: true, characterData: true, subtree: true };

        this.observerSenderos_.observe(target, config);
      // });
    }
  }

  gestionClusterEquipamientos(zoomClusterEquipamientos) {
    //Debe estar cargado el plugin de TOC
    let equipamientos = this.layersWFS_.find(layer => layer.name == "equipamientos_uso_publico");
    //Estilo cluster
    if(M.utils.isUndefined(this.facadeMap_.getPlugins("SelectByPolygon")[0]) || !this.facadeMap_.getControls("SelectByPolygon")[0].activated){ //Para no se agrupe el cluster con el plugin de selección desactivado o cuando no exista
      let capa = this.facadeMap_.getLayers().find(layer => layer.name == "equipamientos_uso_publico");

      let visible = capa.isVisible();

      if(visible){
        if(this.facadeMap_.getZoom() >= zoomClusterEquipamientos){
          equipamientos.setStyle();
        }else{
          equipamientos.setStyle(this.equipComposite_st_);
        }
      }
      // let control = this.facadeMap_.getControls("ActiveLayers")[0];
      // let capas = control.getTemplateVariables_().then((respuesta) => {
      //   let capa = null;
      //   for (let i = 0; i <= respuesta.length; i++) {
      //     if(respuesta[i].name == "equipamientos_uso_publico"){
      //       capa = respuesta[i];
      //       break;
      //     }
      //   }
      //   let visible = capa.visible;

      //   if(visible){
      //     if(this.facadeMap_.getZoom() >= zoomClusterEquipamientos){
      //       equipamientos.setStyle();
      //     }else{
      //       equipamientos.setStyle(this.equipComposite_st_);
      //     }
      //   }    
      // });
    }
  }

  setCapaBase() {
    if(!M.utils.isUndefined(this.facadeMap_.getPanels("managelayers")[0])){
      let contenedor = document.getElementById("tool-baselayers");
      // let contenedor = this.facadeMap_.getControls("BaseLayers")[0].getControlContainer_();
      let bases = contenedor.querySelector("div[id='toc-baselayers']").children;
      //Movemos el mapa blanco al final
      let ultimoHijo = contenedor.querySelector("div[id='toc-baselayers']").lastElementChild;
      let baseBlanco = this.getBaseBlanco(bases);
      this.insertarDespues(baseBlanco, ultimoHijo);

      //Activar capa base indicada
      for (let i = 0; i < bases.length; i++) {
        if(bases[i].id == "base_" + this.responseJsonNegocio_.capaBaseActiva){
          bases[i].classList.add("baseActivo");
          let baseSeleccionado = this.findItemByProperty(this.facadeMap_.getBaseLayers(), "id", bases[i].id);
          if (baseSeleccionado) {
            baseSeleccionado.setVisible(true);
          }
        }else{
          bases[i].classList.remove("baseActivo");
          let baseNoSeleccionado = this.findItemByProperty(this.facadeMap_.getBaseLayers(), "id", bases[i].id);
          if (baseNoSeleccionado) {
            baseNoSeleccionado.setVisible(false);
          }
        }
      }
    }
  }

  getBaseBlanco(bases) {
    for (let i = 0; i < bases.length; i++) {
      if(bases[i].id == "base_blanco") return bases[i];
    }
  }

  insertarDespues(nuevoNodo, nodoAnterior) {
    nodoAnterior.parentNode.insertBefore(nuevoNodo, nodoAnterior.nextSibling);
  }

  findItemByProperty(lstItems, itemProperty, itemValue) {
    let item = null;
    if (lstItems && itemProperty) {
      lstItems.some((it) => {
        if (it[itemProperty] === itemValue) {
          item = it;
          return true;
        }
      });
    }
    return item;
  }

  //Recibe array de objetos json con los códigos (array) y el tipo
  filterFeaturesBySearch(filter) {
    if(this.featuresFiltradas_.length > 0) this.cleanSearchFilter();
    if(!M.utils.isUndefined(filter) && !M.utils.isNullOrEmpty(filter)){
      let filtro = [];
      let filtros = [];
      filter.forEach((element) => {
        //Preparamos los filtros para cada tipo
        switch (element.tipo) {
          case 'equipamiento':
            filtro = this.prepareFilterArray(element, "CODIGOEQUI");
            filtros.push({
              layersName: ["equipamientos_uso_publico", "Centros de Visitantes", "Puntos de Información", "Ecomuseos"],
              filtro: filtro
            });
            break;
          case 'sendero':
            var senderos = this.facadeMap_.getLayers().find(x=>x.name == "senderos");
            var carriles = this.facadeMap_.getLayers().find(x=>x.name == "Carriles Cicloturista");
            var elementSenderos = [];
            var elementCarriles = [];
            
            var senderosVisibleStatus = senderos.isVisible();
            var carrilesVisibleStatus = carriles.isVisible();
            senderos.setVisible(true);
            carriles.setVisible(true);

            element.codigos.forEach((f)=>{
              if(senderos.getFeatures().find(x=>x.getGeoJSON().properties.CODIGOEQUI == f) != null){
                elementSenderos.push(f);
              }
              if(carriles.getFeatures().find(x=>x.getGeoJSON().properties.CODIGOEQUI == f) != null){
                elementCarriles.push(f);
              }
            });


            if(elementSenderos.length > 0){
              var filtroSenderos = this.prepareFilterArray({"tipo": "sendero", "codigos": elementSenderos}, "CODIGOEQUI");
              filtros.push({
                layersName: ["senderos"], filtro: filtroSenderos
              });
            }
            if(elementCarriles.length > 0){
              var filtroCarriles = this.prepareFilterArray({"tipo": "Carriles Cicloturista", "codigos": elementCarriles}, "CODIGOEQUI"); 
              filtros.push({
                layersName: ["Carriles Cicloturista"], filtro: filtroCarriles
              });
            }

            senderos.setVisible(senderosVisibleStatus);
            carriles.setVisible(carrilesVisibleStatus);
            /*filtro = this.prepareFilterArray(element, "CODIGOEQUI");
            filtros.push({
              layersName: ["senderos", "Carriles Cicloturista"],
              filtro: filtro
            });*/
            break;
          case 'geoparque':
            filtro = this.prepareFilterArray(element, "CODIGOESPA");
            filtros.push({
              layersName: ["geoparques"],
              filtro: filtro
            });
            break;
          case 'zepim':
            filtro = this.prepareFilterArray(element, "CODIGOESPA");
            filtros.push({
              layersName: ["ZEPIM"],
              filtro: filtro
            });
            break;
          case 'reserva':
            filtro = this.prepareFilterArray(element, "CODIGOESPA");
            filtros.push({
              layersName: ["reservas_biosfera"],
              filtro: filtro
            });
            break;
          case 'patrimonio':
            filtro = this.prepareFilterArray(element, "CODIGOESPA");
            filtros.push({
              layersName: ["patrimonio_humanidad"],
              filtro: filtro
            });
            break;
          case 'ramsar':
            filtro = this.prepareFilterArray(element, "RAM_CODE");
            filtros.push({
              layersName: ["humedales_ramsar"],
              filtro: filtro
            });
            break;
          case 'redNatura':
            filtro = this.prepareFilterArray(element, "CODIGOEURO");
            filtros.push({
              layersName: ["RedNatura2000"],
              filtro: filtro
            });
            break;
          case 'espacioNatural':
            filtro = this.prepareFilterArray(element, "CODIGOESPA");
            filtros.push({
              layersName: ["Parques Nacionales", "Parques Naturales", "Parajes Naturales", "Reservas Naturales", "Paisajes Protegidos", "Monumentos Naturales", "Reservas Naturales Concertadas", "Parques Periurbanos"],
              filtro: filtro
            });
            break;
        }
      });

      filtros.forEach((element) => {
        this.setLayerFilter(element.layersName, element.filtro); 
      });
    }       
  }

  prepareFilterArray(element, atributo) {
    let arrayFilter = [];
    element.codigos.forEach((codigo) => {
      arrayFilter.push(M.filter.EQUAL(atributo, codigo.toString()));
    });
    return arrayFilter;
  }

  setLayerFilter(layersName, filterArray) {
    layersName.forEach((layerName) => {
      let ly = this.facadeMap_.getLayers().find(layer => layer.name == layerName);

      if(ly.getFeatures().length != 0){
        this.setFilterToLayer(ly, filterArray);
      }else{
        ly.setVisible(true);
        ly.on(M.evt.LOAD, () => {
          this.setFilterToLayer(ly, filterArray);
        });
      }
    });
  }

  setFilterToLayer(ly, filterArray) {
    if(ly.name == "equipamientos_uso_publico") {
      let initialFilter = M.filter.NOT(M.filter.OR(
        [M.filter.EQUAL("TIPOEQUIPA", "CENTRO DE VISITANTES"),
        M.filter.EQUAL("TIPOEQUIPA", "PUNTO DE INFORMACION"), 
        M.filter.EQUAL("TIPOEQUIPA", "ECOMUSEO")]));

      let filter = M.filter.AND([initialFilter, M.filter.OR(filterArray)]);
      ly.setFilter(filter);

      //Guardamos el filtro para establecerlo de nuevo según la escala
      this.equipFilter_ = filter;

      //Evento para quitar el filtro a un determinado nivel de zoom
      this.facadeMap_.getMapImpl().getView().on('change:resolution', this.visibilidadEqipFiltrados, this);

      let lyFeatures = ly.getFeatures();
      if(lyFeatures.length > 0){
        if(!ly.isVisible()) ly.setVisible(true);
        lyFeatures.forEach((feature) => {
          this.featuresFiltradas_.push(feature);
        });

        //Zoom al filtro
        this.zoomToFilter();
      }

      if(this.featuresFiltradas_.length == 0) this.cleanSearchFilter();
    }else if (ly.name == "senderos") {
      let initialFilter = M.filter.EQUAL("MODALIDAD", "Sendero señalizado");

      let filter = M.filter.AND([initialFilter, M.filter.OR(filterArray)]);
      ly.setFilter(filter);

      //Guardamos el filtro establecerlo de nuevo según la escala
      this.senderoFilter_ = filter;

      //Desconectamos el observador del TOC
      this.observerSenderos_.disconnect();

      //Evento para quitar el filtro a un determinado nivel de zoom
      this.facadeMap_.getMapImpl().getView().on('change:resolution', this.visibilidadSenderoFiltrados, this);

      let lyFeatures = ly.getFeatures();
      if(lyFeatures.length > 0){
        lyFeatures.forEach((feature) => {
          this.featuresFiltradas_.push(feature);
        });

        //Activamos la visibilidad de la capa
        if(!ly.isVisible()) ly.setVisible(true);

        //Zoom al filtro
        this.zoomToFilter();
      }

      if(this.featuresFiltradas_.length == 0) this.cleanSearchFilter();
    }else{
      let filter = M.filter.OR(filterArray);

      ly.setFilter(filter);

      //Activamos el evento para quitar el filtro de Centro de Visitantes, Puntos de Información y Ecomuseos
      if(ly.name == "Centros de Visitantes" || ly.name == "Puntos de Información" || ly.name == "Ecomuseos") {
        switch (ly.name) {
          case "Centros de Visitantes":
          this.CVFilter_ = filter;
          break;
          case "Puntos de Información":
          this.PIFilter_ = filter;
          break;
          case "Ecomuseos":
          this.EcoFilter_ = filter;
          break;
        }         
        this.facadeMap_.getMapImpl().getView().on('change:resolution', this.visibilidadEqipFiltrados, this);
      }

      //Activamos el evento para quitar el filtro de carriles cicloturista
      if(ly.name == "Carriles Cicloturista") {
        ly.setVisible(true);
        this.carrilFilter_ = filter;

        //Desconectamos el observador del TOC
        this.observerSenderos_.disconnect();

        //Evento para quitar el filtro a un determinado nivel de zoom       
        this.facadeMap_.getMapImpl().getView().on('change:resolution', this.visibilidadSenderoFiltrados, this);
        this.visibilidadSenderoFiltrados();
      }

      let lyFeatures = ly.getFeatures();
      if(lyFeatures.length > 0){
        if(!ly.isVisible()) ly.setVisible(true);
        lyFeatures.forEach((feature) => {
          this.featuresFiltradas_.push(feature);
        });

        //Zoom al filtro
        this.zoomToFilter();
      }

      if(this.featuresFiltradas_.length == 0) this.cleanSearchFilter();
    }
  }

  cleanSearchFilter() {
    let layers = this.facadeMap_.getLayers();
    layers.forEach((layer) => {
      if(layer instanceof M.layer.WFS || layer instanceof M.layer.GeoJSON){
        layer.removeFilter();
      }
    });

    //Limpiamos el array de con las features del filtro y mostramos el mapa a extensión completa
    this.featuresFiltradas_ = [];
    this.facadeMap_.zoomToMaxExtent();

    //Establecemos los filtros originales
    this.setFiltroEquipamientosClear();
    this.setFiltroSenderosClear();

    //Eliminamos el nivel de escala a la capa original de equipamientos y senderos
    this.facadeMap_.getMapImpl().getView().un('change:resolution', this.visibilidadEqipFiltrados, this);
    this.facadeMap_.getMapImpl().getView().un('change:resolution', this.visibilidadSenderoFiltrados, this);

    //Desactivamos la visibilidad de la capa de senderos y carriles cicloturista
    let senderosycarriles = this.layersWFS_.find(layer => layer.name == "senderos");
    senderosycarriles.setVisible(false);
    let carriles = this.layersWFS_.find(layer => layer.name == "Carriles Cicloturista");
    carriles.setVisible(false);

    //Activamos el observador del senderos del TOC
    let target = document.getElementById("tool-activelayers");
    // let target = this.facadeMap_.getControls("ActiveLayers")[0].getControlContainer_();
    let config = { attributes: true, childList: true, characterData: true, subtree: true };
    this.observerSenderos_.observe(target, config);
    this.carrilFilter_ = null;
    this.senderoFilter_ = null;
  }

  zoomToFilter() {
    //Usamos el array con las features filtradas para hacer zoom a las mismas
    let extent = this.featuresFiltradas_[0].getImpl().getOLFeature().getGeometry().getExtent().slice(0);
    this.featuresFiltradas_.forEach((feature) => {
      ol.extent.extend(extent,feature.getImpl().getOLFeature().getGeometry().getExtent());
    });
    this.facadeMap_.getMapImpl().getView().fit(extent);
    if(this.facadeMap_.getZoom() >= this.responseJsonNegocio_.zoomFiltro){
      this.facadeMap_.setZoom(this.responseJsonNegocio_.zoomFiltro - 1);
    }
  }

  setFiltroEquipamientosClear() {
    let equipamientos = this.layersWFS_.find(layer => layer.name == "equipamientos_uso_publico");

    let initialFilter = M.filter.NOT(M.filter.OR(
      [M.filter.EQUAL("TIPOEQUIPA", "CENTRO DE VISITANTES"),
      M.filter.EQUAL("TIPOEQUIPA", "PUNTO DE INFORMACION"), 
      M.filter.EQUAL("TIPOEQUIPA", "ECOMUSEO")]));
    equipamientos.setFilter(initialFilter);
  }

  setFiltroSenderosClear() {
    let senderosycarriles = this.layersWFS_.find(layer => layer.name == "senderos");

    senderosycarriles.setFilter(M.filter.EQUAL("MODALIDAD", "Sendero señalizado"));    
    senderosycarriles.setStyle(this.senderos_st_);
  }

  visibilidadEqipFiltrados() {
    let equipamientos = this.layersWFS_.find(layer => layer.name == "equipamientos_uso_publico");
    let centros_visitantes = this.layersWFS_.find(layer => layer.name == "Centros de Visitantes");    
    let puntos_informacion = this.layersWFS_.find(layer => layer.name == "Puntos de Información");     
    let ecomuseos = this.layersWFS_.find(layer => layer.name == "Ecomuseos");
    
    if(this.facadeMap_.getZoom() >= this.responseJsonNegocio_.zoomFiltro){
      this.setFiltroEquipamientosClear();
      centros_visitantes.removeFilter();
      puntos_informacion.removeFilter();
      ecomuseos.removeFilter();
    } else {
      equipamientos.setFilter(this.equipFilter_);
      centros_visitantes.setFilter(this.CVFilter_);
      puntos_informacion.setFilter(this.PIFilter_);
      ecomuseos.setFilter(this.EcoFilter_);
    }
  }


  visibilidadSenderoFiltrados() {
    let senderosycarriles = this.layersWFS_.find(layer => layer.name == "senderos");
    let carriles = this.layersWFS_.find(layer => layer.name == "Carriles Cicloturista");
    
    if(this.facadeMap_.getZoom() >= this.responseJsonNegocio_.zoomFiltro){
      this.setFiltroSenderosClear();
      carriles.removeFilter();
    } else {
      senderosycarriles.setFilter(this.senderoFilter_);
      senderosycarriles.setVisible(true);
      
      if(this.carrilFilter_ != null){
        carriles.setFilter(this.carrilFilter_);
        carriles.setVisible(true);
        carriles.setStyle(new M.style.Line({fill: {}, stroke: {color: 'rgb(175,48,83)', width: 4, linedash: [15,7]}}));
      }
    }
  }

  //Recibe un json con el tipo y el id del espacio sobre el que hacer zoom en el mapa
  zommToElementById(element) {
    if(!M.utils.isUndefined(element) && !M.utils.isNullOrEmpty(element)){
      let result = null;
      let layersName = [];
      switch (element.tipo) {
          case 'equipamiento':
            layersName = ["equipamientos_uso_publico", "Centros de Visitantes", "Puntos de Información", "Ecomuseos"];
            this.findLayerElementById(layersName, "CODIGOEQUI", element.codigo).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
              this.drawMarker(respuesta);
              // this.facadeMap_.drawFeatures(respuesta);
            });
            break;
          case 'sendero':
            layersName = ["senderos", "Carriles Cicloturista"];
            this.findLayerElementById(layersName, "CODIGOEQUI", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'geoparque':
            layersName = ["geoparques"];
            this.findLayerElementById(layersName, "CODIGOESPA", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'zepim':
            layersName = ["ZEPIM"];
            this.findLayerElementById(layersName, "CODIGOESPA", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'reserva':
            layersName = ["reservas_biosfera"];
            this.findLayerElementById(layersName, "CODIGOESPA", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            }); 
            break;
          case 'patrimonio':
            layersName = ["patrimonio_humanidad"];
            this.findLayerElementById(layersName, "CODIGOESPA", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'ramsar':
            layersName = ["humedales_ramsar"];
            this.findLayerElementById(layersName, "RAM_CODE", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'redNatura':
            layersName = ["RedNatura2000"];
            this.findLayerElementById(layersName, "CODIGOEURO", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
          case 'espacioNatural':
            layersName = ["Parques Nacionales", "Parques Naturales", "Parajes Naturales", "Reservas Naturales", "Paisajes Protegidos", "Monumentos Naturales", "Reservas Naturales Concertadas", "Parques Periurbanos"];
            this.findLayerElementById(layersName, "CODIGOESPA", element.codigo.toString()).then((respuesta) => {
              this.zoomToElement(respuesta.getImpl().getOLFeature().getGeometry().getExtent(), element.tipo);
            });
            break;
        }
    }
  }

  findLayerElementById(layersArray, atributeName, codigo) {
    return new Promise((resolve, reject) => {
      let feature;
      // let featureEncontrada;
      for (let i = 0; i < layersArray.length; i++) {
        let layer = this.layersWFS_.find(layer => layer.name == layersArray[i]);
        if(layer.getFeatures().length != 0){
          feature = layer.getFeatures().find(feature => feature.getAttribute(atributeName) == codigo);
          if(!M.utils.isUndefined(feature)){
            if(!layer.isVisible()) layer.setVisible(true);
            resolve(feature);
          }
        }else{
          layer.setVisible(true);
          layer.on(M.evt.LOAD, () => {
            feature = layer.getFeatures().find(feature => feature.getAttribute(atributeName) == codigo);
            if(!M.utils.isUndefined(feature)){
             resolve(feature);
           }
          });
        }
      }
    });
  }

  zoomToElement(extent, tipo) {
    if(!M.utils.isNullOrEmpty(extent)){
      if(tipo == 'equipamiento'){
        this.facadeMap_.getMapImpl().getView().fit(extent);
        //this.facadeMap_.setZoom(this.responseJsonNegocio_.zoomFichaEquipamientos);
      }else{
        this.facadeMap_.getMapImpl().getView().fit(extent);
      }
    }
  }

  drawMarker(feature){
    this.facadeMap_.getLayers().find(layer => {
        if(layer.name == '__draw__'){
          layer.clear();
        }
    });

    let markerStyle = new  M.style.Point({icon: {src: 'data:image/png;base64,',
                                                anchor: [0.5, 0], 
                                                anchororigin: 'bottom-left',
                                                anchorxunits: 'fraction',
                                                anchoryunits: 'fraction'
                                                }});
    var marker = new M.Feature("marcador" + Math.random(), {
      "type": "Feature",
      "geometry": {
        "type": "Point",
        "coordinates": feature.getGeometry().coordinates
      }
    });
    marker.setStyle(markerStyle);

    this.facadeMap_.drawFeatures(marker);
  }

  cambiarLeyendaOSM() {
    if(!M.utils.isUndefined(this.facadeMap_.getPanels("managelayers")[0])){
      this.facadeMap_.getControls("BaseLayers")[0].on(M.evt.ACTIVATED, () => {
        let controlContainer = document.getElementById("tool-baselayers");
        // let controlContainer = this.facadeMap_.getControls("BaseLayers")[0].getControlContainer_();
        if(controlContainer.querySelector("div[id='base_0'] img[data-id='base_0']").getAttribute("src") != "data:image/png;base64,"){
          controlContainer.querySelector("div[id='base_0'] img[data-id='base_0']").setAttribute("src", "data:image/png;base64,");
        }
      });
    }
  }
}