/**
 * @module M/plugin/VentanaNegocio
 */

import VentanaNegocioControl from './ventananegociocontrol.js';
import 'assets/css/ventananegocio.css';

export default class VentanaNegocio extends M.Plugin {

  /**
   * Name to identify this plugin
   * @const
   * @type {string}
   * @public
   * @api stable
   */
  static get NAME() {
    return 'ventanaNegocio';
  }

  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(params) {

    super();
    this.name = VentanaNegocio.NAME;
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    /**
     * Control
     * @private
     * @type {Array<M.Control>}
     */
    this.negocioControl_ = null;

    /**
     * Prameters
     * @private
     * @type {Array<M.Control>}
     */
    this.urlJsonNegocio_ = params.urlJsonNegocio;

  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    M.dialog.error = ()=>{};
    this.negocioControl_ = new VentanaNegocioControl();
    this.controls_.push(this.negocioControl_);
    this.map_ = map;
    map.addControls(this.controls_);
  }

  initialLoad() {
    let promesa = this.negocioControl_.getImpl().cargaInicial(this.map_, this.urlJsonNegocio_);
    return promesa;
  }

  performOperations() {
    let promesa = this.negocioControl_.getImpl().ejecutarOperaciones();
  }

  filterBySearch(filter) {
    this.negocioControl_.getImpl().filterFeaturesBySearch(filter);
  }

  cleanSearch() {
    this.negocioControl_.getImpl().cleanSearchFilter();
  }

  zoomToElement(element) {
    this.negocioControl_.getImpl().zommToElementById(element);
  }
}
